@import '~@ikea-ux/typography/_body-fonts.scss';


body .formular {
  margin: 20px 0 50px 0;
  &.submitState {
    pointer-events: none;
    a {
      pointer-events: auto;
    }
  }
}

.text-center {
  text-align: center;
}
.center {
  display: flex;
  justify-content: center;
}

.blue-ikea-icon {
  width: 140px;
  height: 140px;
  display: block;
  margin: 0 auto;
}

.blue-ikea-icon__new-kitchen {
  width: 180px;
  height: auto;
}

.blue-ikea-icon__lieferung {
  width: 300px;
  height: auto;
}

.debug-filler {
  position: relative;
  z-index: 1;
  opacity: 0.5;
  .debug-filler-inner {
    position: absolute;
    top: -20px;
    right: 0;
    font-size: 10px;
    button {
      font-size: 10px;
      font-family: monospace;
      border: 0;
      background-color: rgba(0, 0, 0, 0.8);
      padding: 0 10px;
      color: #00FF00;
      border-radius: 2px;
      margin: 0 1px 0 0;
    }
  }
}

.init-loading {
  margin-top: 50px;
}

//#app {
//  margin-top: 106px;
//}

.btn--icon .btn__copy .svg-icon, a.btn--icon .btn__copy .svg-icon {
  fill: #111111;
}

.calendar-lightbox {
  .modal {
    text-align: left;
  }
}
.form-field.remove-margin-bottom {
  margin-bottom: 0;
}

.date-calender-section {
  > .form-field {
    margin-bottom: 0;
  }
}

.room-measurement-section {
  > .form-field {
    margin-bottom: 0;
  }
}

.contact-section {
  .family-loader-container {
    position: relative;
    .loading-spinner {
      position: absolute;
      z-index: 1;
      right: 20px;
      top: 21px;
    }
  }
  .privacy-checkbox {
    margin-top: 48px;
  }
}

.calendar {
  &.isLoading {
    pointer-events: none;
  }
  position: relative;
  transition: width 1000ms;
  .fake-input {
    cursor: pointer;
    .form-field {
      pointer-events: none;
      input {
        padding-left: 3.45rem;
      }
      .loading-spinner {
        position: absolute;
        top: 21px;
        left: 15px;
      }
    }
    &.disabled {
      pointer-events: none;
      .svg-icon {
        opacity: 0.4;
      }
    }
  }
  .calendar-nav {
    display: flex;
    align-items: center;
    .calendar-header-text {
      padding: 0 15px;
      flex-grow: 1;
      text-align: center;
      @media screen and (max-width: $breakpoints-large) {
        font-size: 12px;
      }
    }
    .dtd-direction-right-switch-left {
      .svg-icon {
        transform: rotate(180deg);
      }
    }
  }
  .calendar-content {
    overflow: hidden;
    .calendar-scroll-wrapper {
      overflow: visible;
      .calendar-scroll {
        max-height: 500px;
        overflow: scroll;
      }
    }
    .calendar-day {
      padding-top: 5px;
      border-bottom: 1px solid $colour-neutral-grey-200;
      &:last-child {
        border-bottom: 0;
      }
      .calendar-day-title {
        margin: 8px 0 0 0;
      }
      .no-timeslots-text {
        @extend .body-font-small;
        margin: 0 0 12px 0;
      }
      .calendar-timeslots {
        margin: 10px 0 21px 0;
        .calendar-day-button {
          margin: -2px 0 0 -2px;
          border-radius: 0;
          position: relative;
          top: 2px;
          left: 2px;
          &.active {
            background: $colour-neutral-grey-900;
            color: $colour-neutral-white;
          }
        }
      }
    }
  }

  .calendar-content-loading {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 444px;
  }

}


.vb {
  > .vb-dragger {
    z-index: 5;
    width: 17px;
    right: 0;
    > .vb-dragger-styler {
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      -webkit-transform: rotate3d(0, 0, 0, 0);
      transform: rotate3d(0, 0, 0, 0);
      -webkit-transition: background-color 100ms ease-out, margin 100ms ease-out, height 100ms ease-out;
      transition: background-color 100ms ease-out, margin 100ms ease-out, height 100ms ease-out;
      background-color: $colour-neutral-grey-800;
      opacity: 0.1;
      margin: 5px 5px 5px 5px;
      border-radius: 20px;
      height: calc(100% - 10px);
      display: block;
    }
  }
  &.vb-scrolling-phantom > .vb-dragger > .vb-dragger-styler {
    opacity: 0.3;
  }
  > .vb-dragger:hover > .vb-dragger-styler, &.vb-dragging > .vb-dragger > .vb-dragger-styler {
    opacity: 0.5;
    margin: 4px;
    height: 100%;
  }
  &.vb-dragging-phantom > .vb-dragger > .vb-dragger-styler {
    opacity: 0.5;
  }
}

.resizer {
  position: absolute !important;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.submit-section {
  .alert {
    margin: 1rem 0;
  }
}

.error-page {
  margin-bottom: 50px !important;
}

.form-field [role="group"] {
 .radio {
   @media (min-width: $breakpoints-medium) {
     display: inline-block;
     margin-right: 1.5rem;
   }
 }
}

body .success {
  margin: 20px 0 50px 0;
  .body-font-large {
    margin-top: 34px;
  }
}

body .cancel-page {
  margin: 20px 0 50px 0;
  h1 {
    margin-bottom: 50px;
  }
}

//overwrite styleguide grid col-4 for IE11
.styleguide .ikea-col-lg-4 {
  padding: 1rem 0;
}
