$font-family-text: "Noto Sans IKEA", "Noto Sans", "Roboto", "Open Sans", sans-serif;
$baseline: 16;

$font-weight-normal: 400;
$font-weight-bold: 700;

$page-headline-font-size-l: px2rem(60);
$page-headline-line-height-l: px2lineheight(60, 72);
$page-headline-letter-spacing-l: -1.1px;
$page-headline-font-size-m: px2rem(48);
$page-headline-line-height-m: 1.1667;
$page-headline-letter-spacing-m: -.84px;
$page-headline-font-size-s: px2rem(38);
$page-headline-line-height-s: 1.2632;
$page-headline-letter-spacing-s: -.67px;

@mixin page-headline {
  font-size: $page-headline-font-size-s;
  line-height: $page-headline-line-height-s;
  letter-spacing: $page-headline-letter-spacing-s;

  @media all and (min-width: $breakpoints-medium) {
    font-size: $page-headline-font-size-m;
    line-height: $page-headline-line-height-m;
    letter-spacing: $page-headline-letter-spacing-m;
  }

  @media all and (min-width: $breakpoints-large) {
    font-size: $page-headline-font-size-l;
    line-height: $page-headline-line-height-l;
    letter-spacing: $page-headline-letter-spacing-l;
  }
}

$title-font-size-h1-l: px2rem(48);
$title-line-height-h1-l: 1.1667;
$title-letter-spacing-h1-l: -.84px;
$title-font-size-h1-m: px2rem(38);
$title-line-height-h1-m: 1.2632;
$title-letter-spacing-h1-m: -.67px;
$title-font-size-h1-s: px2rem(30);
$title-line-height-h1-s: 1.3334;
$title-letter-spacing-h1-s: -.53px;

@mixin h1 {
  font-size: $title-font-size-h1-s;
  line-height: $title-line-height-h1-s;
  letter-spacing: $title-letter-spacing-h1-s;

  @media all and (min-width: $breakpoints-medium) {
    font-size: $title-font-size-h1-m;
    line-height: $title-line-height-h1-m;
    letter-spacing: $title-letter-spacing-h1-m;
  }

  @media all and (min-width: $breakpoints-large) {
    font-size: $title-font-size-h1-l;
    line-height: $title-line-height-h1-l;
    letter-spacing: $title-letter-spacing-h1-l;
  }
}

$title-font-size-h2-l: px2rem(38);
$title-line-height-h2-l: 1.2632;
$title-letter-spacing-h2-l: -.67px;
$title-font-size-h2-m: px2rem(30);
$title-line-height-h2-m: 1.3334;
$title-letter-spacing-h2-m: -.53px;
$title-font-size-h2-s: px2rem(24);
$title-line-height-h2-s: 1.3334;
$title-letter-spacing-h2-s: -.42px;

@mixin h2 {
  font-size: $title-font-size-h2-s;
  line-height: $title-line-height-h2-s;
  letter-spacing: $title-letter-spacing-h2-s;

  @media all and (min-width: $breakpoints-medium) {
    font-size: $title-font-size-h2-m;
    line-height: $title-line-height-h2-m;
    letter-spacing: $title-letter-spacing-h2-m;
  }

  @media all and (min-width: $breakpoints-large) {
    font-size: $title-font-size-h2-l;
    line-height: $title-line-height-h2-l;
    letter-spacing: $title-letter-spacing-h2-l;
  }
}

$title-font-size-h3-m: px2rem(30);
$title-line-height-h3-m: 1.3334;
$title-letter-spacing-h3-m: -.53px;
$title-font-size-h3-s: px2rem(24);
$title-line-height-h3-s: 1.3334;
$title-letter-spacing-h3-s: -.42px;

@mixin h3 {
  font-size: $title-font-size-h3-s;
  line-height: $title-line-height-h3-s;
  letter-spacing: $title-letter-spacing-h3-s;

  @media all and (min-width: $breakpoints-medium) {
    font-size: $title-font-size-h3-m;
    line-height: $title-line-height-h3-m;
    letter-spacing: $title-letter-spacing-h3-m;
  }
