$title-font-size-h4-m: px2rem(24);
$title-line-height-h4-m: 1.3334;
$title-letter-spacing-h4-m: -.42px;
$title-font-size-h4-s: px2rem(20);
$title-line-height-h4-s: px2lineheight(20, 24);

@mixin h4 {
  font-size: $title-font-size-h4-s;
  line-height: $title-line-height-h4-s;

  @media all and (min-width: $breakpoints-medium) {
    font-size: $title-font-size-h4-m;
    line-height: $title-line-height-h4-m;
    letter-spacing: $title-letter-spacing-h4-m;
