$title-font-size-h5: px2rem(20);
$title-line-height-h5: px2lineheight(20, 24);

@mixin h5 {
  font-size: $title-font-size-h5;
  line-height: $title-line-height-h5;
}

$body-font-size-l: px2rem(18);
$body-line-height-l: 1.3334;

$body-font-size-m: px2rem(16);
$body-line-height-m: px2lineheight(16, 24);

$body-font-size-s: px2rem(14);
$body-line-height-s: 1.1429;

$body-font-size-xs: px2rem(12);
$body-line-height-xs: 1.3334;

@mixin body-l {
  font-size: $body-font-size-l;
  line-height: $body-line-height-l;
}

@mixin body-m {
  font-size: $body-font-size-m;
  line-height: $body-line-height-m;
}

@mixin body-s {
  font-size: $body-font-size-s;
  line-height: $body-line-height-s;
